.Row {
  width: 3vw;
  height: 3vw;
  display: grid;
  grid-template-columns: repeat(10, 3vw);
  grid-column-gap: max(0.1vw, 1px);
  color: red;
  font-size: 2.5vw;
}

.Column {
  width: 3vw;
  height: 3vw;
  text-align: center;
  line-height: 3vw;
  background-color: #77b4ee;
  text-shadow: 1px 1px 2px black;
  border: max(0.1vw, 1px) solid white;
}

@media only screen and (max-width: 600px) {
  .Row {
    width: 9vw;
    height: 9vw;
    font-size: 9vw;
    grid-template-columns: repeat(10, 9vw);
  }

  .Column {
    width: 9vw;
    height: 9vw;
    line-height: 9vw;
  }
}
